  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.descripcion {
  word-wrap: break-word;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  max-width: 110rem;
  margin: auto;
}

.espacio {
  width: 100%;
  margin-bottom: 7rem;
}

.espacio_p {
  padding-bottom: 4rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 15rem;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 600;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
