}
.alerta.error {
  background-color: #BA0534;
}
.alerta.exito {
  background-color: #9F425A;
}

.no {
  display: none !important;
}

.inicio {
  display: block;
}

.no2 {
  display: none !important;
}

.imghabitaciones {
  display: block;
}

.cuadro {
  position: absolute;
  top: 13.5rem;
  width: 100%;
  height: 50rem;
  background-color: rgba(0, 0, 0, 0.507);
  z-index: 2;
}

.video33 {
  display: flex;
  width: 100%;
  height: 50rem;
  align-items: end;
  display: flex;
  justify-content: left;
}
.video33 .header-flex {
  padding: 3rem;
  z-index: 4;
  align-items: center;
  display: flex;
  justify-content: center;
  border-radius: 50%;
}
.video33 .header-flex .part2_header-flex p:nth-child(1) {
  font-size: 4rem;
  color: #ffffff;
}
.video33 .header-flex .part2_header-flex p:nth-child(2) {
  font-size: 1.4rem;
  color: #ffffff;
}
.video33 .header-flex .part2_header-flex p:nth-child(3) {
  font-size: 1.4rem;
  color: #CF5A60;
  font-style: italic;
}

video {
  width: 143rem;
}

.video {
  position: absolute;
  top: 13.5rem;
  height: 50rem;
  width: 100%;
  overflow: hidden;
  z-index: 1;
}

.redes {
  display: flex;
  gap: 2rem;
}
.redes .redes_div {
  background-color: #ffffff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
}
.redes .redes_div a i {
  color: #CF5A60;
}

.header {
  background-color: #8a8383;
  height: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .header_cont {
  align-items: center;
  display: flex;
  justify-content: space-between;
  gap: 4rem;
}
.header .header_cont .navegacion {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header .header_cont .navegacion a {
  display: inline-block;
  color: #ffffff;
  font-size: 1.3rem;
  padding: 1rem;
}
.header .header_cont .navegacion a:hover {
  padding: 1rem;
  background-color: #f2f0f0;
  color: #CF5A60;
  border-radius: 1rem;
  -webkit-transition: ease-out 0.3s;
  -moz-transition: ease-out 0.3s;
  transition: ease-out 0.3s;
}

.imagen_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .img_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 60rem;
  background-color: hsla(0, 0%, 8%, 0.524);
}
.imagen_header .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.slideshow {
  width: 100%;
  height: 60rem;
  position: relative;
  overflow: hidden;
}
.slideshow ul, .slideshow ol {
  list-style: none;
  margin: 0;
}

.slideshow2 {
  width: 100%;
  height: 40rem;
  position: relative;
  overflow: hidden;
}
.slideshow2 ul, .slideshow2 ol {
  list-style: none;
  margin: 0;
}

.slideshow3 {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.slideshow3 ul, .slideshow3 ol {
  list-style: none;
  margin: 0;
}

.slider li, ul,
.slider2 li, ul,
.slider3 li, ul {
  width: 100%;
}

.slider li,
.slider2 li,
.slider3 li {
  overflow: hidden;
}

.slider li .img_slider {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  animation-duration: 4s;
  animation-name: slidein;
  object-fit: cover;
}

.slider2 li .img_slider {
  width: 100%;
  height: 40rem;
  overflow: hidden;
  animation-duration: 4s;
  border-radius: 3px;
}

.slider3 li .img_slider {
  width: 100%;
  height: 40rem;
  overflow: hidden;
  animation-duration: 4s;
  border-radius: 3px;
}

@keyframes slidein {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
.slider .caption {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 15px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  z-index: 1;
}

.slider .caption h1 {
  font-size: 50px;
}

.slider .caption p {
  margin-top: 10px;
  font-size: 20px;
}

.pagination {
  position: absolute;
  top: 102%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.pagination li {
  font-size: 20px;
  margin: 2px 5px;
  color: #909196;
  cursor: pointer;
}

.left, .right {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 35px;
  cursor: pointer;
  z-index: 2;
}

.left {
  left: 10px;
}

.right {
  right: 10px;
}

.total {
  margin-bottom: 10px;
  border: 0;
}

.slider_eventos {
  position: relative;
}

.slideshow, .slidershow3 {
  overflow: hidden;
}

.slider2 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slider3 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slider2 li {
  flex: 0 0 100%;
}

.slider3 li {
  flex: 0 0 100%;
}

.img_slider {
  width: 100%;
  height: 40rem;
  object-fit: cover;
}

.imagen_header2,
.imagen_header3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.imagen_header_fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.texto_overlay {
  color: white;
  font-size: 1.6rem;
  text-align: center;
  max-width: 80%; /* Ajusta el ancho m√°ximo del texto seg√∫n tus necesidades */
  max-height: 80%; /* Ajusta la altura m√°xima del texto seg√∫n tus necesidades */
  overflow: hidden; /* Recorta el texto si es demasiado largo */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.texto_overlay span {
  font-weight: 600;
}
.texto_overlay a {
  display: inline-block;
  margin-top: 1rem;
  color: #fff;
  font-style: italic;
  font-size: 1.4rem;
}

.imagen_header2 {
  width: 100%;
  height: 40rem;
}
.imagen_header2 .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 40rem;
}
.imagen_header2 .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

#btnWs {
  padding: 10px;
  background-color: green;
  color: white;
}

@media screen and (max-width: 600px) {
  .slider .caption p {
    display: none;
  }
  .slider .caption h1 {
    font-size: 35px;
  }
  .left, .right {
    font-size: 30px;
  }
  .slider li img {
    width: 600px;
  }
  .pagination {
    display: none;
  }
}
